@charset "utf-8";

@font-face {
	font-family: 'SpoqaHanSansNeo';
	font-style: normal;
	font-weight: 300;
	src: url(assets/fonts/SpoqaHanSansNeo-Light.eot);
	src: local('SpoqaHanSansNeoLight'),
			 url(assets/fonts/SpoqaHanSansNeo-Light.eot?#iefix) format('embedded-opentype'),
			 url(assets/fonts/SpoqaHanSansNeo-Light.woff2) format('woff2'),
			 url(assets/fonts/SpoqaHanSansNeo-Light.woff) format('woff'),
			 url(assets/fonts/SpoqaHanSansNeo-Light.ttf) format('truetype');
	font-display: fallback;
}

